
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: center;
  height: 100vh;
  margin: 80px auto;
}
#game {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-radius: 50%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 400px;
  height: 400px;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
}
@media screen and (max-width: 778px) {
#game {
      width: 300px;
      height: 300px;
}
}
.disabled {
  display: none;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1000;
  cursor: not-allowed;
}
.gameColor {
  display: block;
  width: 200px;
  height: 200px;
  opacity: 0.8;
  cursor: pointer;
}
@media screen and (max-width: 778px) {
.gameColor {
      width: 150px;
      height: 150px;
}
}
.gameColor:hover {
  opacity: 1;
}
#customization {
  margin: 10px;
  font-size: 20px;
}
#Round {
  font-size: 25px;
}
#game_result {
  font-size: 20px;
  position: absolute;
  top: 30px;
  width: 100%;
  font-size: 30px;
}
